import { Component, inject, TemplateRef } from '@angular/core';
import { NgbOffcanvas } from '@ng-bootstrap/ng-bootstrap';
import { MultiThemeService } from './multi-theme.service';

@Component({
  selector: 'ang-multi-theme',
  templateUrl: './multi-theme.component.html',
  styles: [],
})
export class MultiThemeComponent {
  private offcanvasService = inject(NgbOffcanvas);
  selectedTheme = '';

  constructor(private service: MultiThemeService) {}

  openThemeCanvas(content: TemplateRef<any>) {
    this.offcanvasService.open(content, { position: 'end', backdrop: false });
  }

  changeTheme() {
    this.service.loadTheme(this.selectedTheme);
  }
}
